@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
.slide-image {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.slide-image img {
    width: 100%;
}

.slide {
    position: relative;
    width: 100%;
    max-height: 638px;
}

.slide-content {
    position: absolute;
    top: 35%;
    left: 8%;
    display: flex;
    flex-direction: column;
    justify-content: left;
    text-align: left;
}

.slide-content h1 {
    font-family: Pacifico;
    font-size: 60px;
    font-weight: 400;
    color:#10504F;
}

.slide-content-light h1 {
    color: #fff;
}

.slide-content p {
    font-family: Lato;
    font-size: 26px;
    font-weight: 400;
    color: #10504F;
}

.slide-content-light p {
    color: #fff;
}

.slide-content button {
    font-family: Lato;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 17px;
    color: #fff;
    background-color: black;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    height: 43px;
    width: 129px;
    border: none;
}

@media screen and (max-width: 768px) {
    .slide {
        position: unset;
        background-color: #EFEFEF;
    }
    .slide-content {
        position: unset;
        padding: 40px;
        text-align: center;
        justify-content: center;
        align-items: center;
    }
    .slide-content h1 {
        font-size: 26px;
    }
    .slide-content p {
        font-size: 14px;
    }
    .slide-content button {
        font-size: 12px;
    }
    .image-gallery-bullets {
        bottom: 55%;
    }
    .slide-content-light h1 {
        color: #10504F;
    }
    .slide-content-light p {
        color: #10504F;
    }
}
.App {
  text-align: center;
}

.App-header {
  background-color: #10504F;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Lato', sans-serif;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: white;
  height: 190px;
}

.Announcement-bar {
  background-color: black;
  display: flex;
  width: 100%;
  height: 55px;
  font-size: 14px;
  align-items: center;
  justify-content: center;
}

.NavBar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
}

.Navbar-mobile-menu {
  display: none;
  width: 20%;
  justify-content: center;
}

.Navbar-logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 20%;
  height: 50%;
}

.Navbar-logo img {
  height:100%;
}

.NavBar-links {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 60%;
}

.Nav-link {
  text-decoration: none;
  color: white;
  font-size: 20px;
}

.Navbar-icons {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 20%;
}

.Navbar-icon {
  background-color: transparent;
  border: none;
}

.App-link {
  color: #61dafb;
}

@media screen and (max-width: 768px) {
  .App-header {
    height: 108px;
  }
  .NavBar-links {
    display: none;
  }
  .Navbar-logo {
    width: 50%;
    height: 70%;
    justify-content: left;
  }
  .Navbar-icons {
    width: 30%;
  }
  .Navbar-mobile-menu {
    display: flex;
  }
}
.product-slide {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-slide-2 {
    display: none;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
}

.product-slide-4 {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    max-width: 1440px;
}

.product {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.product a {
    text-decoration: none;
    color: #000;
}

.product-image img {
    width: 100%;
}

.product-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.product-title {
    font-family: 'Lato', sans-serif;
    font-size: 22px;
}

.product-description {
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    text-transform: uppercase;
}

.product-price {
    font-family: 'Pacifico', cursive;
    font-size: 26px;
    color: #10504F;
}

@media (max-width: 1120px) {
    .product-slide-2 {
        display: flex;
    }
    .product-slide-4 {
        display: none;
    }
}

@media screen and (max-width: 768px) {
    .product-image {
        max-width: 146px;
    }
    .product-title {
        font-size: 16px;
    }
    .product-description {
        font-size: 10px;
        font-weight: 300;
    }
    .product-price {
        font-size: 18px;
    }
}
.footer {
    display: flex;
    flex-direction: row;
    background-color: black;
    padding-top: 60px;
    padding-bottom: 60px;
    padding-left: 50px;
    padding-right: 50px;
}

.link-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-right: 100px;
}

.menu-title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.menu-title a {
    display: none;
    font-family: 'Pacifico';
    font-size: 22px;
    font-weight: normal;
}

.menu-title p {
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
}

.link-menu h3 {
    color: white;
    font-family: 'Pacifico';
    font-size: 22px;
    font-weight: normal;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
}

.link-menu ul {
    display: flex;
    height: 100%;
    flex-direction: column;
    font-family: 'Lato';
    font-size: 16px;
    line-height: 2.5em;
    padding-left: 0;
}

.link-menu a {
    color: white;
    text-decoration: none;
}

.social-menu {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: flex-end;
}

.social-menu ul {
    display: flex;
    flex-direction: row;
}

.social-menu li {
    margin-left: 50px;
}

@media screen and (max-width: 768px) {
    .footer {
        flex-direction: column;
        padding: 20px;
    }
    .link-menu {
        margin-right: 0;
    }
    .menu-title {
        width: 100%;
    }
    .menu-title a {
        display: flex;
    }
    .social-menu {
        align-items: flex-start;
    }
    .social-menu li {
        margin-left: 0;
        margin-right: 50px;
    }

    .link-menu ul {
        line-height: 0;
    }

    .expandable {
        overflow: hidden;
      
        transition: all .5s ease-in-out;
        line-height: 0;
        padding: 0 1em;
        color: transparent;
    }

    .expandable:target {
        line-height: 2.5em;
    }
}
.index-content {
  padding:0;
  margin:0;
  background-color: white;
}

.collection-list {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1440px;
  padding: 20px;
}

.collection {
  display: flex;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 130px;
  margin: 10px;
  position: relative;
  max-width: 435px;
  overflow: hidden;
  align-items: center;
}

.collection img {
  display: block;
  width: 100%;
  height: auto;
}

.collection-title {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0%;
  width: 100%;
  height: 25%;
  font-family: 'Pacifico', cursive;
  font-weight: 400;
  color: white;
  background-color: #10504F;
}

.collection-title p {
  font-size: 40px;
}

.product-slider {
  margin: 0 auto;
  max-width: 1440px;
  text-align: center;
}

.hero-image-still {
  width: 100%;
  position: relative;
  overflow: hidden;
  background-color: #10504F;
}

.hero-image-still img {
  width: 100%;
}

.hero-image-still-content {
  display: flex;
  flex-direction: column;
  justify-content: left;
  position: absolute;
  top: 10%;
  right: 5%;
}

.hero-image-still-content h1 {
  font-family: 'Pacifico', cursive;
  color: white;
  -webkit-margin-before: unset;
          margin-block-start: unset;
  -webkit-margin-after: unset;
          margin-block-end: unset;
}

.hero-image-still-content h2 {
  font-family: 'Lato', sans-serif;
  font-size: 26px;
  font-weight: normal;
  color: white;
  -webkit-margin-before: unset;
          margin-block-start: unset;
  -webkit-margin-after: unset;
          margin-block-end: unset;
}

.hero-image-still-content button {
  color: white;
  font-family: 'Lato';
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  border: none;
  background-color: #10504F;
  width: 129px;
  height: 43px;
  margin-top: 26px;
}

.newsletter {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #10504F;
  padding-top: 75px;
  padding-bottom: 75px;
}

.newsletter-title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.newsletter-title img {
  height: 36px;
  margin-right: 10px;
}

.newsletter-title h1 {
  color: white;
  font-weight: 400;
}

.newsletter-form {
  display: flex;
  flex-direction: row;
  max-width: 1440px;
  margin-left: 30px;
  margin-right: 30px;
  justify-content: center;
  align-items: center;
}

.newsletter-form p {
  font-family: 'Lato', sans-serif;
  font-size: 22px;
  font-weight: normal;
  color: white;
  min-width: 50%;
  text-align: left;
}

.newsletter-form input {
  font-family: 'Lato';
  font-size: 22px;
  color: #C6C6C6;
  padding: 15px;
  width: 100%;
  height: 36px;
  border: none;
}

.newsletter-form button {
  background-color: transparent;
  color: white;
  font-family: 'Lato';
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  border: white 2px solid;
  padding: 1.75em;
  margin-left: 13px;
}

h1 {
  font-family: 'Pacifico', cursive;
  font-size: 60px;
  font-weight: 400;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (max-width: 768px) {
  .collection-title p {
    font-size: 20px;
  }
  .collection-title {
    height: 15%;
  }
  .collection-last {
    max-width: 100%;
    max-height: 250px;
  }
  .hero-image-still {
    position: unset;
    max-height: unset;
    overflow: unset;
  }
  .hero-image-still-content {
    position: unset;
    background-color: #EFEFEF;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .hero-image-still-content h1 {
    font-size: 26px;
    color: #10504F;
  }
  .hero-image-still-content h2 {
    font-size: 14px;
    color: #10504F;
  }
  .hero-image-still-content button {
    background-color: black;
  }

  .newsletter-title {
    flex-direction: column;
  }

  .newsletter-title img {
    height: 20px;
  }

  .newsletter-title h1 {
    font-size: 20px;
  }

  .newsletter-form {
    flex-direction: column;
  }

  .newsletter-form p{
    text-align: center;
    font-size: 14px;
  }

  .newsletter-form input {
    display: none;
  }

  .newsletter-form button {
    padding-left: 15%;
    padding-right: 15%;
    margin-top: 30px;
  }

  h1 {
    font-size: 26px;
  }
}
